'use client';

import { Button, Table, type TableColumnType } from 'antd';

const ETFList = ({ dataSource }: { dataSource: any[] }) => {
  const columns: TableColumnType<any>[] = [
    {
      title: 'ETF代码',
      dataIndex: 'code',
      key: 'code',
    },
    {
      title: 'ETF名称',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: '类别',
      dataIndex: 'category',
      key: 'category',
    },
    {
      title: '市场',
      dataIndex: 'market',
      key: 'market',
    },
    {
      title: '最新价',
      dataIndex: 'price',
      key: 'price',
    },
    {
      title: '涨跌幅',
      dataIndex: 'change',
      key: 'change',
    },
    {
      title: '成交量',
      dataIndex: 'volume',
      key: 'volume',
    },
    {
      title: '操作',
      dataIndex: 'action',
      key: 'action',
      render: (text: string, record: any) => (
        <Button type="primary" icon={<i className="fas fa-edit"></i>}>
          编辑
        </Button>
      ),
    },
  ];

  return (
    <div className="bg-white rounded-lg shadow-card">
      <Table dataSource={dataSource} columns={columns} />
    </div>
  );
};

export default ETFList;
